import React from "react";
import columnsOptions from "./columnsOptions";
import data from "./assignData";
import SpreadSheet from "../../app/common/form/SpreadSheet";
import { useRef } from "react";
import { useSelector } from "react-redux";

export default function AssignPage() {
  const { members } = useSelector((state) => state.member);
  const { projects } = useSelector((state) => state.project);

  const handleChange = (instance, cell, x, y, value) => {
    console.log(cell);
    console.log(x);
    console.log(y);
    console.log(value);
  };

  const options = useRef({
    columns: columnsOptions(members, projects),
    onbeforechange: handleChange,
  });

  return <SpreadSheet options={options} data={data} />;
}
